.logoSection {
    height: calc(100vh - 52px - 44px);
    align-items: center;
    justify-content: center;
    display: flex;
    position: absolute;
    margin-left: auto;
    margin-right: auto;
    width: 980px;
    z-index: 1;
    padding-bottom: 5px;
}
.textSection {
    position: absolute;
    clip: rect(1px, 1px, 1px, 1px);
    clip-path: inset(0px 0px 99.9% 99.9%);
    overflow: hidden;
    height: 1px;
    width: 1px;
    padding: 0;
    border: 0;
    font-size: 40px;
    line-height: 1.1;
    font-weight: 600;
    letter-spacing: 0em;
    display: inline-block;
    color: transparent;
    -webkit-background-clip: text;
    background-clip: text;
    background-image: linear-gradient(
        90deg,
        #2ca2b4,
        #5598de 24%,
        #7f87ff 45%,
        #f65aad 76%,
        #ec3d43
    );
    will-change: transform;
    padding-top: 0.5px;
    overflow: hidden;
    background-color: #2ca2b4 0%;
    padding-bottom: 0.03em;
    margin-bottom: -0.03em;
}

.bigDiv {
    perspective: 1600px;
    perspective-origin: 50% 650px;
}
.bigText {
    font-size: 120px;
    line-height: 1;
    font-weight: 600;
    letter-spacing: -0.023em;
    font-family: SF Pro Display, SF Pro Icons, Helvetica Neue, Helvetica, Arial,
        sans-serif;
    transition: all 600ms ease;
    transform-style: preserve-3d;
    transform: rotateY(-40deg);
    margin-right: 10px;
}

.container {
    width: 100%;
    height: 662px;
    display: flex;
}
.specsContainer {
    perspective-origin: 50% 690px;
}

.specsContainer {
    perspective: 1600px;
    perspective-origin: 50% 520px;
}
.productName {
    font-size: 32px;
    line-height: 1.125;
    font-weight: 600;
    letter-spacing: 0.004em;
    font-family: SF Pro Display, SF Pro Icons, Helvetica Neue, Helvetica, Arial,
        sans-serif;
    transform: rotateY(-45deg);
}
.productSize {
    font-size: 96px;
    line-height: 1;
    font-weight: 600;
    letter-spacing: -0.009em;
    font-family: SF Pro Display, SF Pro Icons, Helvetica Neue, Helvetica, Arial,
        sans-serif;
    transform: rotateY(-45deg);
}

.specsContainer {
    perspective: 1600px;
    perspective-origin: 50% 520px;
}

